import React from "react";
import { withStyles } from "@material-ui/styles";
import styles from "./styles/MiniPaletteStyles";
import DeleteIcon from "@material-ui/icons/Delete"; 


function MiniPalette(props) {
  const { classes, paletteName, emoji, colors, id } = props;

  function deletePalette(e){
    e.stopPropagation();
    props.openDeleteDialog(id)
  }

  const miniColorBoxes = colors.map(color => (
    <div
      className={classes.miniColor}
      style={{ backgroundColor: color.color }}
      key={color.name}
    />
  ));
  console.log("minipalette: ", id);
  return (
    <div className={classes.root} onClick={()=>{props.handleClick(id)}}>      
      <DeleteIcon className={classes.deleteIcon} onClick={deletePalette}/>     
      <div className={classes.colors}>{miniColorBoxes}</div>
      <h5 className={classes.title}>
        {paletteName} <span className={classes.emoji}>{emoji}</span>
      </h5>
    </div>
  );
}

export default React.memo(withStyles(styles)(MiniPalette), (prevProps, nextProps)=>{
  if (prevProps.id === nextProps.id) return true
});
