module.exports = {
    "@global":{
        ".page": {
            height: "100vh",
            position: "fixed",
            width: "100%",
            top: 0,
            transition: "transform 0.5s ease-in-out"
        },
        ".page-enter": {
            transform: "translateX(-100%)"
        },
        ".page-enter-active": {
            transform: "translateX(0%)"
        },
        ".page-exit-active": {
            transform: "translateX(100%)"
        },
        ".slideRight": {
            height: "100vh",
            position: "fixed",
            width: "100%",
            top: 0,
            transition: "transform 0.5s ease-in-out"
        },
        ".slideRight-enter": {
            transform: "translateX(100%)"
        },
        ".slideRight-enter-active": {
            transform: "translateX(0%)"
        },
        ".slideRight-exit-active": {
            transform: "translateX(-100%)"
        }
    }

}