import React from 'react';
import clsx from 'clsx';
import PaletteFormNav from "./PaletteFormNav";  
import ColorPickerForm  from "./ColorPickerForm";
import Drawer from '@material-ui/core/Drawer';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import Button from "@material-ui/core/Button";
import DraggableColorList from "./DraggableColorList";
import {arrayMove} from "react-sortable-hoc";
import {drawerWidth, useStyles} from "./styles/NewPaletteFormStyles";
import seedColors from "./seedColors";

export default function NewPaletteForm(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);   
  const [colors, setColors] = React.useState(seedColors[0].colors);
  const {maxColors = 20, history, palettes, savePalette, setPaletteSaved} = props; 
  const paletteIsFull = colors.length >= maxColors;
  
  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  function removeColor(colorName){
    setColors(colors.filter(({name}) => name !== colorName ))
  }

  function clearColors(){
    setColors([]);
  }

  function addRandomColor(){
    // const allColors = props.palettes.map(p=>p.colors).flat();
    const allColors = seedColors.map(p=>p.colors).flat();
    let rand, randomColor;
    let isDuplicateColor = true;
    while(isDuplicateColor){
      rand = Math.floor(Math.random() * allColors.length);
      randomColor = allColors[rand];
      isDuplicateColor = colors.some(color=>color.name === randomColor.name)
    }
    setColors([...colors, allColors[rand]])
  }

  const onSortEnd = ({oldIndex, newIndex}) => {
    setColors(arrayMove(colors, oldIndex, newIndex));
  }  
  // console.log("newpaletteform")
  const PaletteFormNavProp = {colors, classes, open, history, handleDrawerOpen, palettes, savePalette, setPaletteSaved}
  return (
    <div className={classes.root}>
      <PaletteFormNav {...PaletteFormNavProp}/>
      <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="left"
        open={open}
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div className={classes.drawerHeader}>
          <IconButton onClick={handleDrawerClose}>
            <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider />
        <div className={classes.container}>
          <Typography variant="h4" gutterBottom>Design your palette</Typography>
          <div className={classes.buttons}>
            <Button className={classes.button} variant="contained" color="secondary" onClick={clearColors}>Clear palette</Button>
            <Button className={classes.button} variant="contained" color="primary" onClick={addRandomColor} disabled={paletteIsFull}>Random color</Button>
          </div>
          <ColorPickerForm colors={colors} maxColors={maxColors}  setColors={setColors} paletteIsFull={paletteIsFull} drawerWidth={drawerWidth}/>  
          </div>
      </Drawer>
      <main
        className={clsx(classes.content, {
          [classes.contentShift]: open,
        })}
      >
        <div className={classes.drawerHeader} />
        <DraggableColorList axis="xy" colors={colors} removeColor={removeColor} onSortEnd={onSortEnd}/>        
      </main>
    </div>
  );
}

