import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { ValidatorForm, TextValidator } from "react-material-ui-form-validator";
import {Picker} from "emoji-mart";
import "emoji-mart/css/emoji-mart.css";

export default function FormDialog(props) {
  console.log(props);
  const [stage, setStage] = React.useState("form");
  const [paletteName, setPaletteName] = React.useState("");
  const [emoji, setEmoji] = React.useState("");

  React.useEffect(()=>{ValidatorForm.addValidationRule("paletteNameUnique", value=>
    props.palettes.every(({paletteName}) => paletteName.toLowerCase() !== value.toLowerCase()))}, 
    [props.palettes]
  )
  React.useEffect(()=>{
    handleSubmit(props.history)
  }, [emoji])

  function handleSubmit(history){
    if (emoji){
      const newPalette = {
        paletteName, 
        id: paletteName.toLowerCase().replace(/ /g, "-"),
        colors: props.colors,
        emoji
      }
      props.setPaletteSaved(true);
      props.savePalette(newPalette, history);
      setStage("");
      // props.history.push("/");         
      // props.history.push({pathname: "/", state: {newPalette}});
   }
  }

  function handleChange (e){
    setPaletteName(e.target.value)
  }

  function savePalette(x){
    setEmoji(x.native)      
  }

  return (  
    <div>   
      <Dialog open={stage === "emoji"} onClose={()=>{props.setFormShowing(false)}}>
        <DialogTitle id="form-dialog-title">Choose a palette emoji</DialogTitle>
        <Picker onSelect={savePalette} title="Pick a palette emoji"/> 
      </Dialog> 
      <Dialog open={stage === "form"} onClose={()=>{props.setFormShowing(false)}} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Choose a palette name</DialogTitle>
        <ValidatorForm onSubmit={()=>setStage("emoji")}>
        <DialogContent>
          <DialogContentText>
            Please enter a name for your new palette. Make sure the name is unique.
          </DialogContentText>          
                    <TextValidator 
                        name="setPaletteName" 
                        label="Palette name" 
                        value={paletteName} 
                        fullWidth
                        margin="normal"
                        onChange={handleChange}
                        validators={["required", "paletteNameUnique"]}
                        errorMessages={["Enter Palette name", "Palette name already taken"]}
                    />                
                    
        </DialogContent>
        <DialogActions>
          <Button onClick={()=>{props.setFormShowing(false)}} color="primary">
            Cancel
          </Button>
          <Button  variant="contained" color="primary" type="submit">Save Palette</Button>
        </DialogActions>
        </ValidatorForm>
      </Dialog>
    </div>    
  );
}