import React from "react";
import {withStyles} from "@material-ui/styles";
import DeleteIcon from "@material-ui/icons/Delete";
import {SortableElement} from "react-sortable-hoc";
import styles from "./styles/DraggableColorBoxStyles";

export default SortableElement(withStyles (styles)(function DraggableColorBox(props){
    const {classes, name, handleClick, color} = props;
    const [deleteBox, setDeleteBox] = React.useState(false)

    function deleteAnimation(e){
        // console.log(e);
        setDeleteBox(true);
        setTimeout(()=>{
            handleClick(name)
        }, 500);
        const elements = document.getElementsByClassName(name);
        for (let index = elements.length - 1; index >= 0; index--) {
            elements[index].parentNode.removeChild(elements[index]);
        }
        // console.log(elements);        
    }


    return(
        <div className={`${classes.root} ${deleteBox && classes.rootDelete}`} style={{backgroundColor: color}}>
            <div className={classes.boxContent}>
                <span className={name}>{name}</span>
                <DeleteIcon className={`${classes.deleteIcon} ${name}`} onClick={deleteAnimation}/>
            </div>            
        </div>
    )
})
)