import React from 'react';
import TextField from '@material-ui/core/TextField';
import Button from "@material-ui/core/Button";
import {ChromePicker} from "react-color";
import {withStyles} from "@material-ui/core/styles";
import styles from "./styles/ColorPickerFormStyles";



export default withStyles(styles)(function ColorPickerForm (props){
    const {classes, colors, maxColors, setColors, paletteIsFull} = props;
    const [currentColor, setCurrentColor] = React.useState("teal");
    const [newColorName, setNewColorName] = React.useState("");
    const [colorValidationError, setColorValidationError] = React.useState(false);
    const [colorValidationErrorMessage, setColorValidationErrorMessage] = React.useState("");
    const [nameValidationError, setNameValidationError] = React.useState(false);
    const [nameValidationErrorMessage, setNameValidationErrorMessage] = React.useState("");
  
    function addNewColor(e){
        e.preventDefault();
        if(checkColor(currentColor) && checkName(newColorName)) {
          setColors([...colors, {color: currentColor, name: newColorName}]);
          setNewColorName("")
          // document.getElementById("standard-error-helper-text").value=""
        }
    }

    const checkColor = (colorToCheck) => {
        if(colors.every(({color}) => color !== colorToCheck)) {
          setColorValidationError(false);
          setColorValidationErrorMessage("");
          return true;
        }
        setColorValidationError(true);
        setColorValidationErrorMessage("This color is already used")
    }
    
    const checkName = (nameToCheck) => {
        if(colors.every(({name}) => name.toLowerCase() !== nameToCheck.toLowerCase())) {
          setNameValidationError(false);
          setNameValidationErrorMessage("");
          return true;
        }
        setNameValidationError(true);
        setNameValidationErrorMessage("This name is not unique");
    }

    function updateCurrentColor (newColor){
        if(checkColor(newColor.hex)) return setCurrentColor(newColor.hex);
    }

    function handleChange (e){
        checkColor(currentColor);
        checkName(e.target.value);
        setNewColorName(e.target.value)
    }

    return(
        <div>
            <ChromePicker className={classes.picker} color={currentColor} onChange={updateCurrentColor}/>  
            <form autoComplete="off" onSubmit={addNewColor}>
                
                    <TextField
                        required
                        className={classes.textfield}
                        error={colorValidationError || nameValidationError}
                        id="standard-error-helper-text"
                        label={colorValidationErrorMessage || nameValidationErrorMessage}
                        onChange={handleChange}  
                        value={newColorName}
                        name="setNewColorName"
                        placeholder="color name goes here..."  
                        variant="filled"                                                     
                    />
                    <Button className={classes.addColor} variant="contained" type="submit" color="primary" disabled={paletteIsFull} style={{backgroundColor: paletteIsFull ? "grey" : currentColor}}>{colors.length>=maxColors ? "Palette Full" : "Add color"}</Button>
                
            </form>
        </div>
    )
})