import React, {Component} from "react";
import ColorBox from "./ColorBox";
import Navbar from "./Navbar";
import Footer from "./PaletteFooter";
import {Link} from "react-router-dom";
import { withStyles } from "@material-ui/styles";
import styles from "./styles/PaletteStyles";

export default withStyles(styles) (class SingleColorPalette extends Component {
    constructor(props){
        super(props)
        this._shades = this.gatherShades(this.props.palette, this.props.colorId)
        this.changeFormat = this.changeFormat.bind(this);
        this.state = {format: "hex"};
        // const slideBackFunction = () => {
        //     if (this.props.setSlideBack) {
        //         console.log("slideback is true")
        //         this.props.setSlideBack(true); 
        //     } 
        // }

        // window.addEventListener("popstate", slideBackFunction)
    }

    gatherShades(palette, colorToFilterBy){
        let shades = [];
        let allColors = palette.colors;

        for (let key in allColors){
            shades = shades.concat(
                allColors[key].filter(color=> color.id === colorToFilterBy)
            )
        }
        return shades.slice(1); 
    }

    changeFormat(val) {
        this.setState({ format: val });
      }

    render(){
        const {format} = this.state;
        const {paletteName, emoji, id} = this.props.palette;
        const {classes} = this.props;
        const colorBoxes = this._shades.map(color =>{
            return <ColorBox key={color.name} name={color.name} background={color[format]} showingFullPalette={false}/>
        }
         )
        return(
            <div className={classes.Palette}>
                <Navbar handleChange={this.changeFormat} showingAllColors={false}/>
                <div className={classes.colors}>
                    {colorBoxes}
                    <div className={classes.goBack}><Link to={{pathname: `/palette/${id}`, state: true}}>GO BACK</Link></div>
                </div>
                
                <Footer paletteName={paletteName} emoji={emoji} />
            </div>
        )
    }
})