import React from "react";
import { Route, Switch, withRouter } from "react-router-dom";
import Palette from "./Palette";
import PaletteList from "./PaletteList";
import seedColors from "./seedColors";
import { generatePalette } from "./colorHelpers";
import SingleColorPalette from "./SingleColorPalette";
import NewPaletteForm from "./NewPaletteForm";
import {TransitionGroup, CSSTransition} from "react-transition-group";
import Page from "./Page";

function App (props) {
  const {history, location} = props;
  const savedPalettes = JSON.parse(window.localStorage.getItem("palettes"));
  const [palettes, setPalettes] = React.useState(savedPalettes || seedColors);
  const [paletteSaved, setPaletteSaved] = React.useState(false);

  React.useEffect(()=>{
    syncLocalStorage();
    if (paletteSaved) {
      setPaletteSaved(false);
      props.history.push("/")
    }
  }, [palettes]);

  function findPalette(id) {
    return palettes.find(function(palette) {
      return palette.id === id;
    });
  }
  
  function savePalette(newPalette, history){
    setPalettes([...palettes, newPalette])
  }

  function deletePalette(id){
    setPalettes(palettes.filter(palette=>palette.id !== id))
  }

  function syncLocalStorage (){
    window.localStorage.setItem("palettes", JSON.stringify(palettes))
  }

  if (location.pathname.includes("/singleColor/") && history.action !== "POP") window.previousLocation = location.pathname;
  let animationClassNames = "slideRight";
  if (location.pathname === "/" || location.state) animationClassNames = "page";
  else if (window.previousLocation && history.action==="POP" && !location.pathname.includes("/singleColor/")) {
    animationClassNames = "page";
    window.previousLocation = ""
  }
        
  return (        
    <TransitionGroup childFactory = { child => React.cloneElement(child, {classNames: animationClassNames})}>
      <CSSTransition key={location.key} classNames={animationClassNames} timeout={500}>
        <Switch location={location}>
          <Route exact path="/palette/new" render={routeProps=><Page>
            <NewPaletteForm savePalette={savePalette} palettes={palettes} {...routeProps} setPaletteSaved={setPaletteSaved}/></Page>} />
          <Route exact path='/' 
            render = {routeProps =>{return (<Page><PaletteList palettes={palettes} {...routeProps} deletePalette={deletePalette} /></Page>)}} 
          />
          <Route exact path='/palette/:id' 
            render = {routeProps => {
                        return ( <Page><Palette palette={generatePalette(findPalette(routeProps.match.params.id))} /></Page> )
                      }
                    } 
          />
          <Route path="/palette/:paletteId/singleColor/:colorId"
            render = { routeProps=><Page><SingleColorPalette colorId={routeProps.match.params.colorId} palette={generatePalette(findPalette(routeProps.match.params.paletteId))} /></Page> }/>
          <Route  
            render = {routeProps =>{return (<Page><PaletteList palettes={palettes} {...routeProps} deletePalette={deletePalette} savePalette={savePalette} /></Page>)}} 
          />
        </Switch>
      </CSSTransition>
    </TransitionGroup>
  )
}

export default withRouter(App);
