import React from 'react';
import clsx from 'clsx';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import AddToPhotosIcon from "@material-ui/icons/AddToPhotos";
import Button from "@material-ui/core/Button";
import PaletteMetaForm from "./PaletteMetaForm";

    
export default function PaletteFormNav (props){
    
    const { open, colors, history, handleDrawerOpen, classes, setPaletteSaved } = props;
    const [formShowing, setFormShowing] = React.useState(false);
    // console.log(paletteSaved)
    return (        
        <div>
            <CssBaseline />
            <AppBar
                position="fixed"
                color="default"
                className={clsx(classes.appBar, {
                [classes.appBarShift]: open,
                })}
            >
                <Toolbar>
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    onClick={handleDrawerOpen}
                    edge="start"
                    className={clsx(classes.menuButton, open && classes.hide)}
                >
                    <AddToPhotosIcon />
                </IconButton>
                <Typography variant="h6" noWrap>
                    Create a palette
                </Typography>                
                </Toolbar>
                <div className={classes.navBtns}>
                    <Button className={classes.navBtn} variant="contained" color="secondary" onClick={()=>history.push("/")}>Go back</Button>
                    <Button className={classes.navBtn} variant="contained" color="primary" onClick={()=>{setFormShowing(true)}}>
                        Save palette
                    </Button>
                    {formShowing &&
                        <PaletteMetaForm colors={colors} savePalette={props.savePalette} history={history} palettes={props.palettes} setFormShowing={setFormShowing} setPaletteSaved={setPaletteSaved} />
                    }
                </div>
            </AppBar>
        </div>
    )
}