import React from "react";
import {Link} from "react-router-dom";
import MiniPalette from "./MiniPalette";
import { withStyles } from "@material-ui/styles";
import styles from "./styles/PaletteListStyles";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import CheckIcon from "@material-ui/icons/Check";
import CloseIcon from "@material-ui/icons/Close";
import Avatar from '@material-ui/core/Avatar';
import blue from "@material-ui/core/colors/blue";
import red from "@material-ui/core/colors/red";



function PaletteList (props) {
  const goToPalette = id => {
    return props.history.push(`/palette/${id}`)
  };
  const { palettes, classes } = props;
  const [openDeleteDialog, setOpenDeleteDialog] = React.useState(false);
  const [deletingId, setDeletingId] = React.useState("");
  const openDialog = id => {
     setOpenDeleteDialog(true)
     setDeletingId(id)
  }
  const closeDialog = id => {
    setOpenDeleteDialog(false);
    setDeletingId("");
  }
  const deleteButtonPress = () => {
    setOpenDeleteDialog(false);
    props.deletePalette(deletingId);
    
  }
    return (
      <div className={classes.root}>
        <div className={classes.container}>
          <nav className={classes.nav}>
            <h1 className={classes.heading}>React Colors</h1>
            <Link to="/palette/new">Create a palette</Link>
          </nav>
          <TransitionGroup className={classes.palettes}>
              {palettes.map(palette => (
                <CSSTransition key={palette.id} classNames="fade" timeout={500}>
                  <MiniPalette
                    {...palette}
                    handleClick={goToPalette}
                    openDeleteDialog={openDialog}
                    key={palette.id}
                  />
                </CSSTransition>
              ))}
          </TransitionGroup>          
        </div>
        <Dialog open={openDeleteDialog} aria-labelledby="delete-dialog-title" onClose={closeDialog}>
          <DialogTitle id="delete-dialog-title">Delete this palettes</DialogTitle>
          <List>
            <ListItem button onClick={deleteButtonPress}>
              <ListItemAvatar>
                <Avatar style={{backgroundColor: blue[100], color: blue[600]}}>
                  <CheckIcon/>
                </Avatar>
              </ListItemAvatar>
              <ListItemText>Delete</ListItemText>
            </ListItem>
            <ListItem button onClick={closeDialog}>
              <ListItemAvatar>
                <Avatar style={{backgroundColor: red[100], color: red[600]}}>
                  <CloseIcon/>
                </Avatar>
              </ListItemAvatar>
              <ListItemText>Cancel</ListItemText>
            </ListItem>
          </List>
        </Dialog>
      </div>
    );
  
}
export default withStyles(styles)(PaletteList);
